@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300&family=Martian+Mono:wght@200&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
}
body {
  background: #f3f2ef;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  font-family: "Jost", sans-serif;
}

html {
  font-size: 100%;
  height: 100%;
}

.box {
  width: 20vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  min-width: 20rem;
  min-height: 35rem;
}
.inputData {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  border-radius: 10%;
  /* border: none; */
}
.inputField{
  border-radius:7%;
  border: none;
  height: 5vh;
  background: #dedede;
  text-align: center;
}
input[type="text"]
{
    background: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
}
.wave {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -25rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}
.errorMessage{
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 900;
}
.-three {
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.2;
}
.-two {
  animation: drift 5000ms infinite linear;
  z-index: 3 !important;
  opacity: 0.1;
}
.box:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transform: translate3d(0, 0, 0);
}

@keyframes drift {
  from {
    transform: rotate(360deg);
  }
  from {
    transform: rotate(360deg);
  }
}
.info {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 45%;
  z-index: 4;
}
.location {
  margin-top: 0.5rem;
  text-align: center;
  font-weight: 600;
  font-size: 2.5rem;
}
.fa-street-view {
  animation: rotates 3s linear infinite alternate;
}
@keyframes drift {
  from {
    transform: rotate(360deg);
  }
  from {
    transform: rotate(360deg);
  }
}
.info {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 45%;
  z-index: 4;
}
